import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { SettingSearchResponse } from '../models';
import { HttpClient } from '@angular/common/http';
import { SearchBaseRequest } from '../models/base.model';
import { CacheService } from './cache.service';
import { KeyConst } from '../constants/key.const';

@Injectable({ providedIn: 'root' })
export class SettingService {
    constructor(
        protected httpClient: HttpClient,
        private _cacheService: CacheService
    ) { }

    public search(request: SearchBaseRequest): Observable<SettingSearchResponse> {
        return this.httpClient.get<SettingSearchResponse>(`v1/settings/search`, { params: request as any });
    }

    public useMock(value?: boolean): boolean {
        if (value == true || value == false) {
            this._cacheService.set('mock', value);
            return value;
        }
        var currentValue = <boolean>this._cacheService.get('mock');
        if (!currentValue) return false;
        return currentValue;
    }

    public useKey(key?: string): string {
        if (key) {
            this._cacheService.set(KeyConst.Key, key);
            return key;
        } else {
            var currentKey = <string>this._cacheService.get(KeyConst.Key);
            return currentKey;
        }
    }

    public useSecretKey(key?: string): string {
        if (key) {
            this._cacheService.set(KeyConst.SecretKey, key);
            return key;
        } else {
            var currentKey = <string>this._cacheService.get(KeyConst.SecretKey);
            return currentKey;
        }
    }
}
