import { Injectable } from '@angular/core';
import { PermissionViewModel, UserViewModel } from './auth.model';
import { AuthenticationService } from './auth.service';

@Injectable({ providedIn: 'root' })
export class PermisisonProvider {
  constructor(
    protected authenticationService: AuthenticationService
  ) { }

  public hasPermisison(user: UserViewModel, permisisonKey: string, activityKey: string = null): boolean {
    if (!permisisonKey) return false;
    if (['/', '/auth', '/forbidden', '/not-found'].indexOf(permisisonKey) > -1) return true;
    var currentPermisison = this.retrieveCurrentPermission(user, permisisonKey);
    if (!currentPermisison) return false;
    if (!activityKey) return true;
    var currentActivity = currentPermisison.activities.find(s => s.enabled && s.key == activityKey);
    return currentActivity ? true : false;
  }

  private retrieveCurrentPermission(user: UserViewModel, key: string): PermissionViewModel {
    if (!key || !user) return null;
    let currentPermisison = user.permissions.find(s => s.enabled && key.indexOf(s.key) > -1);
    if (!currentPermisison) {
      for (let i = 0; i < user.roles.length; i++) {
        if (user.roles[i].enabled) {
          for (let j = 0; j < user.roles[i].permissions.length; j++) {
            if (user.roles[i].permissions[j].enabled && key.indexOf(user.roles[i].permissions[j].key) > -1) return user.roles[i].permissions[j];
          }
        }
      }
    }
    return currentPermisison;
  }
}