import { BaseRequest, BaseResponse, SearchBaseRequest, SearchBaseResponse, MenuTab } from '../shared/models/base.model';
import { FileViewModel } from '../shared/file/file.model';

export class UserViewModel {
    id: string;
    fullName: string;
    firstName: string;
    lastName: string;
    userName: string;
    email?: string;
    image?: FileViewModel;
    phone?: string;
    dateOfBirth?: Date;
    externalInfo?: any;
    system?: SystemViewModel = new SystemViewModel();
    roles?: RoleViewModel[] = [];
    permissions?: PermissionViewModel[] = [];
    constructor(init?: Partial<UserViewModel>) {
        Object.assign(this, init);
    }
}

export class AuthenticationViewModel {
    userName: string;
    password: string;
    key?: string;
    secret?: string;
    constructor(init?: Partial<AuthenticationViewModel>) {
        Object.assign(this, init);
    }
}

export class SystemViewModel {
    name?: string;
    key?: string;
    enabled?: boolean;
    constructor(init?: Partial<SystemViewModel>) {
        Object.assign(this, init);
    }
}

export class RoleViewModel {
    name?: string;
    key?: string;
    permissions?: PermissionViewModel[] = [];
    enabled?: boolean;
    constructor(init?: Partial<PermissionViewModel>) {
        Object.assign(this, init);
    }
}

export class PermissionViewModel {
    page?: PageViewModel = new PageViewModel();
    name?: string;
    key?: string;
    enabled?: boolean;
    activities?: ActivityViewModel[] = [];
    constructor(init?: Partial<PermissionViewModel>) {
        Object.assign(this, init);
    }
}

export class PageViewModel {
    name?: string;
    state?: string;
    parent?: PageViewModel;
    icon?: string;
    constructor(init?: Partial<PageViewModel>) {
        Object.assign(this, init);
    }
}

export class ActivityViewModel {
    name?: string;
    key?: string;
    enabled?: boolean;
    constructor(init?: Partial<PageViewModel>) {
        Object.assign(this, init);
    }
}

export class AuthenticationLoginRequest extends BaseRequest<AuthenticationViewModel> {
    token?: string;
    payload: AuthenticationViewModel = new AuthenticationViewModel();
    constructor(init?: Partial<AuthenticationLoginRequest>) {
        super();
        Object.assign(this, init);
    }
}

export class AuthenticationLoginResponse extends BaseResponse<AuthenticationViewModel> {
    user?: UserViewModel;
    menuTabs?: MenuTab[] = [];
    token?: string;
    refreshToken?: string;
    errorMessage?: string;
    constructor(init?: Partial<AuthenticationLoginResponse>) {
        super();
        Object.assign(this, init);
    }
}

