import { Component, ViewChild, ElementRef, AfterViewInit, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationLoginRequest, AuthenticationLoginResponse } from './auth.model';
import { AuthenticationService } from './auth.service';
import { DataService } from '../shared/services/data.service';
import { ValidationService, ValidationOption, ClientValidator, CustomValidationRule, ValidationRuleResponse } from '../shared/validation';
import { fadeInOut } from '../shared/triggers';
import { BaseTemplate } from '../shared/models/base.model';
import { ButtonComponent } from '../shared/button/button/button.component';
import { of } from 'rxjs';
import { SettingService } from '../shared/services/setting.service';
import { CacheService } from '../shared/services/cache.service';
import { AuthConst } from './auth.const';

@Component({
  selector: 'katana-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.scss'],
  animations: [fadeInOut]
})

export class AuthComponent implements OnInit, AfterViewInit {
  @Input() public title: string = 'Permission Authentication System';
  @Input() public validator: ClientValidator;
  @Input() public succeedPath: string;
  @Input() public template: BaseTemplate;
  @Input() public api: string;
  @Input() public success: boolean = true;
  @Output() public completed = new EventEmitter<AuthenticationLoginResponse>();
  @ViewChild('formRef', { static: true }) public formRef: ElementRef;
  @ViewChild('btn', { static: true }) public btn: ButtonComponent;
  public request: AuthenticationLoginRequest = new AuthenticationLoginRequest();
  public errorMessage: string;
  protected mock: AuthenticationLoginResponse;

  constructor(
    public route: ActivatedRoute,
    private _router: Router,
    private _cacheService: CacheService,
    private _settingService: SettingService,
    private _authenticationService: AuthenticationService,
    private _validationService: ValidationService,
    private _dataService: DataService
  ) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      if (data) {
        this._dataService.getToken((response: AuthenticationLoginResponse) => {
          if (data.succeedPath) this.succeedPath = <string>data.succeedPath;
          if (data.success != undefined) this.success = <boolean>data.success;
          if (data.api) this.api = <string>data.api;
          if (data.mock) this.mock = <AuthenticationLoginResponse>data.mock;
          this._authenticationService.setApi(this.api);
          if (response && response.user) {
            this._router.navigate([this.succeedPath]);
          }
        });
      }
    });
  }

  ngAfterViewInit(): void {
    this.initValidations();
  }

  public login(close): void {
    if (!this._validationService.isValid()) {
      close();
      return;
    }
    if (this.useMock()) {
      this.request.mockData = this.mock;
    }
    this.request.payload.key = this._settingService.useKey();
    this.request.payload.secret = this._settingService.useSecretKey();
    this._authenticationService.login(this.request).subscribe((response: AuthenticationLoginResponse) => {
      close();
      if (response.status) {
        this._cacheService.set(AuthConst.Token, response);
        this.completed.emit(response);
        if (this.succeedPath) this._router.navigate([this.succeedPath]);
      } else {
        this._cacheService.remove(AuthConst.Token);
        this.completed.emit(response);
        this.errorMessage = response.errorMessage;
      }
    });
  }

  public trigger(): void {
    this.btn.handleAction({});
  }

  private useMock(): boolean {
    var currentValue = JSON.parse(window.localStorage.getItem('mock'));
    if (!currentValue) return false;
    return currentValue;
  }

  private initValidations(): void {
    if (!this.formRef) throw new Error('formRef is null');
    var options: ValidationOption[] = [
      new ValidationOption({
        validationName: 'Username',
        valueResolver: () => this.request.payload.userName,
        rules: [
          new CustomValidationRule(() => {
            this.errorMessage = null;
            return of(new ValidationRuleResponse({
              status: this.request.payload.userName != undefined && this.request.payload.userName != '',
              message: 'Username khÃ´ng ÄÆ°á»£c Äá» trá»ng'
            }));
          }, true)
        ]
      }),
      new ValidationOption({
        validationName: 'Password',
        valueResolver: () => this.request.payload.password,
        rules: [
          new CustomValidationRule(() => {
            this.errorMessage = null;
            return of(new ValidationRuleResponse({
              status: this.request.payload.password != undefined && this.request.payload.password != '',
              message: 'Password khÃ´ng ÄÆ°á»£c Äá» trá»ng'
            }));
          }, true)
        ]
      })
    ];

    var validator = new ClientValidator({
      formRef: this.formRef,
      options: options,
      payloadRef: () => this
    });

    this._validationService.init({
      validator: this.validator ? this.validator : validator
    });
  }
}
