import { Component, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'katana-daterange-picker',
  templateUrl: './daterange.component.html',
  styleUrls: ['./daterange.component.scss']
})

export class DaterangePickerComponent implements OnDestroy {
  @Input() public title: string;
  @Input() public model: Date[];
  @Input() public disabled: boolean;
  @Input() public minValue: Date;
  @Input() public maxValue: Date;
  @Input() public placeholder: string = 'Äiá»n thÃ´ng tin';
  @Input() public startView: 'month' | 'years' | 'multi-years' = 'month';
  @Input() public pickerType: 'calendar' | 'timer' | 'both' = 'calendar';
  @Input() public pickerMode: 'popup' | 'dialog' = 'popup';
  @Input() public selectMode: 'range' | 'single' | 'rangeFrom' | 'rangeTo' = 'range';
  @Input() public validationName: string;
  @Input() public validationScope: string;
  @Input() emitNullOnDestroy: boolean = false;
  @Output() public modelChange: EventEmitter<Date[]> = new EventEmitter();
  public id: string;

  constructor(protected dataService: DataService) { }

  ngOnInit(): void {
    this.id = this.dataService.newGuid();
  }

  ngOnDestroy() {
    if (this.emitNullOnDestroy)
      this.modelChange.emit(null);
  }

  public getElement(): any {
    var elements = document.getElementsByTagName('input');
    if (elements) {
      for (let i = 0; i < elements.length; i++) {
        if (elements[i].id == this.id) {
          return elements[i];
        }
      }
    }
  }
}
